.menu-bar {
  display: flex;
  justify-content: space-between;
  box-sizing: border-box;
}

.items {
  color: white;
  background-color: transparent;
  font-size: 18px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  box-sizing: border-box;
  padding: 8px 20px;
}

.item{
  margin-right: 18px;
  padding: 20px;
}

.item.title {
  font-weight: 600;
}

button{
  background-color:transparent;
  border: none;
  color:dimgrey;
  font-weight: bold;
  border-radius: 20px;
}

button:hover {
  background-color: rgba(128, 128, 128, 0.115);
}