.App {
  text-align: center;
  align-content: center;
  margin-left: auto;
  margin-right: auto;
}

/* new */

.full-screen {
  min-width: 98vw;
  min-height: 90vh;
}

.center{
  align-items: center;
  justify-content: center;
}

.full-screen-menubar {
  min-width: 100vw-80px;
  min-height: 100vh;
}

.sweet-background{
  background-image: url('https://pub-static.haozhaopian.net/assets/stickers/texture_010/c78603c3-52a1-4f64-9b12-6ef9e69c86be_medium_thumb.jpg');
  background-position: center;
  background-size:contain;
  background-repeat: round;
}

.card {
  width: 500px;
  background-color: white;
  border-radius: 20px;
  padding: 30px;
  box-shadow: 2px 2px 8px 4px rgba(0, 0, 0, 0.1);
}

.item {
  margin: 10px;
}



/*Element*/
.button{
  width: auto;
  height: auto;
  font-size: 15pt;
  padding: 10px;
  margin: 10px;
}

.button:hover{
  background-color: lightgray;
}

.text-box{
  width: auto;
  height: auto;
  padding: 10px;
  margin: 10px;
}

.text-input{
  border-color: lightgray;
  padding: 10px;
  border-radius: 15px;
  font-size: 20px;
}



/*background color*/
.tbg1{
  background-color: rgb(181, 144, 144);
  border: 1px solid black;
}

.tbg2{
  background-color: rgb(103, 162, 82);
  border: 1px solid black;
}

.color-pink{
  background-color: pink;
}

.color-gradient{
  background: linear-gradient(135deg, rgb(105, 180, 255) 0%, rgb(163, 255, 193) 100%);
}

.color-green{
  background-color: limegreen;
}

.color-red{
  background-color:crimson;
}

.color-yellow{
  background-color:gold;
}






/*Text color*/
.text-gray{
  color:dimgrey;
}

.text-pink{
  color: pink;
}

.text-white{
  color: white;
}

.text-red{
  color: crimson;
}

.text-green{
  color: limegreen;
}


/*Text Style*/
.large-header{
  font-size: large;
}

.header{
  font-size: larger;
}